//react 基础组件加载
import * as React from "react";
import { NoticeBar, ListView, Toast } from 'antd-mobile';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../../actions';

import Footer from '../../components/footer';
import AllForm from '../../components/search/allForm';
import FilterComponent from '../../components/search/filterComponent';
import VipItem from '../../components/vipItem'
import CourseEmpty from '../../components/course/CourseEmpty'
import IndexAdvertising from '../../components/advertising/indexAdvertising'
import { getVipList } from '../../api/vip';
import { setLocal, refreshStorage } from '../../util/config'
import { getSeriesTime } from '../../api/index'

import '../../assets/less/courselist.less';


const mapStateToProps = (state: any) => {
    return {
        store: state
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        ...bindActionCreators(Actions, dispatch)
    }
}

interface ISprops {
    [propName: string]: any,
}
interface IState {
    dataSource: any,
    isLoading: Boolean,
    hasMore: Boolean,
    pageFrom: boolean,
    listPad: boolean,
    footType: number,
    query: any,
    filter: any,
    vip_advertising: any,
}


class VipList extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
        });
        this.state = {
            pageFrom: true,
            listPad: false,
            footType: 3,//footerbar
            query: '',
            filter: {
                citys: '',
                start_time: '',
                end_time: '',
                series_id: ''
            },

            dataSource,
            isLoading: true,
            hasMore: true,
            vip_advertising:false,
        }
    }
    courseList: Object[] = [];
    page_num: number = 1;
    page_size: number = 10;

    componentWillMount() {
        let { search } = this.props.match.params;
        this.setState({
            query: search
        }, () => {
            this.queryListData()
        })
        this.checkAd()
    }
    checkAd() {
        //判断是否现实弹窗广告
        let show = localStorage.getItem('vip_advertising');
        if(!show || !refreshStorage('vip_advertising',24*60*60*1000)) {
            setLocal('vip_advertising','true');
            this.setState({vip_advertising: true})
        } else {
            this.setState({vip_advertising: false})
        }
    }
    delayTime() {
        let self = this;
        setTimeout(function () {
            self.setState({
                listPad: true
            })
        }, 3000)
    }
    componentDidMount() {
        document.title = 'VIP活动';
        this.delayTime()
    }
    
    handleFilterChange = (filters) => {
        this.setState({
            filter: { ...filters }
        }, () => {
            this.page_num = 1;
            this.queryListData()
        })
    }
    queryListData() {
        Toast.loading('')
        this.setState({ isLoading: true });
        let { page_num, page_size } = this;
        let { filter, query } = this.state;
        getVipList({
            page_num,
            page_size,
            class_title: query,
            ...filter
        }).then(res => {
            Toast.hide();
            if (!res.status) {
                let { list, total_pages, page_num } = res.data;
                // 新获取的数据直接拼接
                if (page_num === 1) {
                    this.courseList = []
                }
                this.courseList = [...this.courseList, ...list];
                // 如果还有下一页，页码加1
                if (page_num < total_pages) {
                    this.page_num += 1
                }

                this.setState({
                    dataSource: this.state.dataSource.cloneWithRows(this.courseList),
                    isLoading: false,
                    hasMore: page_num < total_pages
                })
                if(filter.series_id !== '') {
                    getSeriesTime({
                        seriesId: filter.series_id
                    })
                }
            }
        })
    }
    onEndReached = (event) => {
        if (this.state.isLoading || !this.state.hasMore) {
            return;
        }
        this.queryListData()
    }

    render() {
        const renderFooter = () => (
            this.state.hasMore &&
            <div style={{ textAlign: 'center', lineHeight: '.26rem' }}>{this.state.isLoading ? 'Loading...' : 'Loaded'}</div>
        )
        const Row = (rowData, sectionID, rowID) => (
            <VipItem key={rowID} course={rowData} />
        )
        const { listPad, footType, query, filter, vip_advertising, isLoading } = this.state;
        const { courseList } = this;
        return (
            <div style={ listPad ? {} : {paddingTop:'.5rem'}}>
                <div className="search-top">
                    {
                        listPad || <NoticeBar mode="closable">课程表仅供参考，最终课程信息请以课前通知为准！</NoticeBar>
                    }
                    <AllForm title="请输入活动名称" searchValues={query} searchForm={1} />
                    <FilterComponent 
                        calendarTit='活动时间'
                        citysTit='活动城市'
                        vipFlag={true} 
                        filters={filter} 
                        onChange={this.handleFilterChange} 
                    />
                </div>
                {   
                    courseList.length === 0 && !isLoading?
                        <CourseEmpty source={0} />
                        :
                        <ListView
                            dataSource={this.state.dataSource}
                            renderFooter={courseList.length === 0 ? () => (<div></div>) : renderFooter}
                            renderRow={Row}
                            className="index-list"
                            pageSize={4}
                            useBodyScroll
                            onScroll={() => { console.log('scroll'); }}
                            scrollRenderAheadDistance={500}
                            onEndReached={this.onEndReached}
                            onEndReachedThreshold={10}
                        />
                }
                {
                    vip_advertising ? 
                        <IndexAdvertising title="vip-layer"/>
                        :
                        <div></div>
                }
                <Footer footType={footType} />
            </div>
        );
    }

}
export default connect(mapStateToProps, mapDispatchToProps)(VipList);