import * as React from "react";


import FootAdvertising from '../../components/advertising/footAdvertising';
import FootBar from '../../components/footer'
import VipList from './vipList'
//redux 基础组件加载
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as Actions from '../../actions';




const mapStateToProps = (state: any) => {
    return {
        store: state
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        ...bindActionCreators(Actions, dispatch)
    }
}

interface ISprops {
    [propsName: string]: any
}
interface IState {
    footType: number
}


class VipIndex extends React.Component<ISprops, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            footType: 3
        }
    }
    componentDidMount() {
        document.title = 'vip活动';
    }
    render() {
        const { footType } = this.state;
        return (
            <div className="vip" style={{paddingBottom: '.5rem',paddingTop: '1rem'}}>
                <VipList match={this.props.match}/>
                <FootAdvertising title="vip-course"/>
                <FootBar  footType={footType}/>
            </div>
        );
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(VipIndex);