import * as React from "react";
import {withRouter} from "react-router";
import {getSeatActiveTime} from "../util"
import '../assets/less/vipItem.less'

interface ISprops {
    [propName: string]: any,
}
interface IState {
    dataSource: any,
    isLoading: Boolean,
    hasMore: Boolean
}
class VipItem extends React.Component<ISprops, IState> {

    handleClickList = (id: string) => {
        //进入详情页
        this.props.history.push('/vipDetail?courseId='+id)
    }
    render() {
        const { course } = this.props;
        const activeTime = getSeatActiveTime(course.start_time || '', course.end_time || '');
        return (
            <div className="vip-item clearfix" key={course.id} onClick={() => this.handleClickList(course.id)}>
                <div className="vip-item-l left">
                    <h2>{course.class_title}</h2>
                    <div className="time">{activeTime}</div>
                    <div className="bottom">
                        <p>{ course.province === '' ? course.city : `${course.province}-${course.city}`}</p>
                        <p>剩余席位：{course.surplus_seat}</p>
                    </div>
                </div>
                <div className="vip-item-r right">
                    <img src={course.img_url} alt={course.course_name} />
                </div>
            </div>
        )
    }

}

//@ts-ignore
export default withRouter(VipItem);
